import React, { Component } from 'react';
import { connect } from 'react-redux';
import './register.css';
import { addNewUser } from '../../actions/user-actions';



class Register extends Component {
    // componentDidMount(){
    //     console.log(this.props.usersReducer)
    // }
    handleRegister = (event) => {
        event.preventDefault();
        event.stopPropagation();
        let form = this.refs.myForm;
        if (form.checkValidity() === false) {
            form.classList.add('was-validated');
        }
        else {
            this.saveToServer();
        }
    }
    functionCall = () => {
        return (<div className="col-md-6 mb-3">
            <label htmlFor="">Phone no.</label>
            <input type="number" className="form-control" ref="phone" placeholder="e.g. +923011234567" required />
            <div className="valid-feedback">
                Looks good!
                        </div>
            <div className="invalid-feedback">
                Please provide a Phone no.
                        </div>
        </div>)

    }
    saveToServer = () => {
        // alert('hello')
        let name = this.refs.name.value
        let email = this.refs.email.value
        let cell = this.refs.phone.value
        let address = this.refs.address.value
        let username = this.refs.username.value
        let password = this.refs.password.value

        let user = { name: name, email: email, cell: cell, address: address, username: username, password: password }

        var options = {
            method: 'POST',
            body: JSON.stringify(user),
            headers: { 'Content-Type': 'application/json' }
        }
        fetch('/addNewUser', options)
            .then((res) => res.json())
            .then((json) => {
                console.log(json)
                this.props.dispatch(addNewUser(json.data));

            })
            .catch((error) => console.log(error))
    }
    render() {
        return (
            <div className="container " style={{ textAlign: 'center', backgroundColor: '#999999', marginTop: '30px', marginBottom: '30px', paddingTop: '46px', paddingBottom: '47px' }}>
                <h1 className='registerHdng'>Register Yourself</h1>

                <form onSubmit={this.handleRegister} method="POST" ref="myForm" className=" registerForm " noValidate>
                    <div className=" form-row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="">Name</label>
                            <input type="text" className="form-control" ref="name" placeholder="e.g. John" required />
                            <div className="valid-feedback">
                                Looks good!
                            </div>
                            <div className="invalid-feedback">
                                Please provide a Name.
                            </div>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="">Email</label>
                            <input type="email" className="form-control" ref="email" placeholder="e.g. abc@abc.com" required />
                            <div className="valid-feedback">
                                Looks good!
                            </div>
                            <div className="invalid-feedback">
                                Please provide a valid Email.
                            </div>
                        </div>
                        {this.functionCall()}
                        <div className="col-md-6 mb-3">
                            <label htmlFor="">Address</label>
                            <input type="text" className="form-control" ref="address" placeholder="e.g. Township, Lahore" required />
                            <div className="valid-feedback">
                                Looks good!
                            </div>
                            <div className="invalid-feedback">
                                Please provide an Address.
                            </div>
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="">Username</label>
                            <input type="text" className="form-control" ref="username" placeholder="e.g. johndoe" required />
                            <div className="valid-feedback">
                                Looks good!
                            </div>
                            <div className="invalid-feedback">
                                Please provide a Username.
                            </div>
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="">Passowrd</label>
                            <input type="password" className="form-control" ref="password" placeholder="Your choice" required />
                            <div className="valid-feedback">
                                Looks good!
                            </div>
                            <div className="invalid-feedback">
                                Please provide a Password.
                            </div>
                        </div>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center' }} className="form-group">

                        <button className="registerBtn" type="submit">Register</button>
                    </div>
                </form>
            </div >




            // <div className="container " style={{ textAlign: 'center', backgroundColor: '#999999', marginTop: '30px', marginBottom: '30px', paddingTop: '46px', paddingBottom: '47px' }}>

            //     <h1 className='registerHdng'>Register Yourself</h1>
            //     <form className='registerForm'>

            //         <div  className="form-row ">
            //             <div class="form-group col-md-6">
            //                 <label for="name">Name:</label>
            //                 <input type="text" class="form-control" id="name" placeholder="Name.." required />
            //             </div>
            //             <div class="form-group col-md-6">
            //                 <label for="email">Email:</label>
            //                 <input type="email" class="form-control" id="email" placeholder="Email.." required />
            //             </div>
            //         </div>

            //         <div  className="form-row ">
            //             <div class="form-group col-md-6">
            //                 <label for="cell">Cell:</label>
            //                 <input type="text" class="form-control" id="cell" placeholder="Cell #.." required />
            //             </div>
            //             <div class="form-group col-md-6">
            //                 <label for="address">Address:</label>
            //                 <input type="text" class="form-control" id="address" placeholder="Address.." required />
            //             </div>
            //         </div>

            //         <div  className="form-row ">
            //             <div class="form-group col-md-6">
            //                 <label for="username">UserName:</label>
            //                 <input type="text" class="form-control" id="username" placeholder="UserName.." required />
            //             </div>
            //             <div class="form-group col-md-6">
            //                 <label for="password">Password:</label>
            //                 <input type="password" class="form-control" id="password" placeholder="Password.." required />
            //             </div>
            //         </div>

            //         <div  className="form-row ">
            //             <div class="form-group col-md-12">
            //                 <button type="submit" ref='register' className="col-md-12 registerBtn">Ok.. Register</button>
            //             </div>
            //         </div>

            //     </form>
            // </div>
        )
    }
}
const mapStateToProps = (store) => {
    return {
        usersReducer: store.usersReducer
    }
}

export default connect(mapStateToProps)(Register)