import React, { Component } from 'react';
import './login.css';




class Login extends Component {

    validateLogin = (evt) => {  //parameter used to gain event information.


        evt.preventDefault();   //this prohibits page to reload.

        var email = this.refs.email
        var password = this.refs.password
        var label = this.refs.loginLabel

        if (email.value.length === 0) {
            label.innerHTML = "Please enter a valid Email";
            email.focus();
        }
        else {
            label.innerHTML = "";
            if (password.value.length === 0) {
                label.innerHTML = "Please enter Password";
                password.focus();
            }
            else {
                label.innerHTML = "";
                this.handleLogin();
                
            }
        }
    }
    handleLogin = () => {

        var options = {
            method: 'POST',
            body: JSON.stringify({ username: this.refs.email.value, password: this.refs.password.value }),
            headers: {
                'Content-Type': 'application/json'
            }
        }
        fetch('/login', options)
            .then((res) => res.json())
            .then((json) => {
                console.log(json);
                this.props.history.push(json.route);
            })
            .catch((error) => console.log(error))
    }

    render() {

        return (

            <div className="container " style={{ textAlign: 'center', backgroundColor: '#999999', marginTop: '60px', marginBottom: '80px', paddingTop: '72px', paddingBottom: '73px' }}>
                <form className='loginForm' onSubmit={this.validateLogin} method='POST'>

                    <h1 className='loginHdng'>Sign In</h1>

                    <div style={{ display: 'inline-block' }} className="form-row col-lg-6 m-0 p-0">
                        <div className='col form-group'>
                            <input type="text" className="form-control form-control-lg" ref="email" placeholder="Enter email" />
                        </div>
                    </div>

                    <br></br>

                    <div style={{ display: 'inline-block' }} className="form-row col-lg-6 m-0 p-0 ">
                        <div className='col form-group'>
                            <input type="password" className="form-control form-control-lg" ref="password" placeholder="Password" />
                        </div>
                    </div>

                    <br></br>

                    {/* <div class="form-check">
                        <input type="checkbox" class="form-check-input" id="exampleCheck1" />
                    </div> */}
                    <div style={{ display: 'inline-block' }} className="form-row  col-lg-5  ">
                        <label className="loginLabel" ref='loginLabel' htmlFor="loginBtn">gshgs</label>
                        <button type="submit" id='loginBtn' ref='login' className="col-sm-12 loginBtn">Sign In</button>
                    </div>

                </form>
            </div>



        )
    }
}


export default Login