import React, { Component } from 'react';
import './css/ohome.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCashRegister } from '@fortawesome/free-solid-svg-icons';
import { faUsers } from '@fortawesome/free-solid-svg-icons';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import { faCubes } from '@fortawesome/free-solid-svg-icons';
import { faUndo } from '@fortawesome/free-solid-svg-icons';
import { faCube } from '@fortawesome/free-solid-svg-icons';
import { faRoute } from '@fortawesome/free-solid-svg-icons';
import { faChartBar } from '@fortawesome/free-solid-svg-icons';


class OHome extends Component {

    openNewPage = (x) => {
        switch (x) {
            case 'sales':
                this.props.history.push('/sales');
                break;
            case 'customers':
                this.props.history.push('/customers');
                break;
            case 'drivers':
                this.props.history.push('/drivers');
                break;
            case 'inventory':
                this.props.history.push('/inventory');
                break;
            case 'returns':
                this.props.history.push('/returns');
                break;
            case 'products':
                this.props.history.push('/products');
                break;
            case 'routes':
                this.props.history.push('/routes');
                break;
            case 'stats':
                this.props.history.push('/stats');
                break;

        }


    }

    render() {



        return (

            <div className="container-fluid " style={{ backgroundColor: '#999999', marginTop: '60px', marginBottom: '60px' }}>
                <div className="container homepage">

                    <div className="row row1">
                        <div className="column">
                            <button onClick={() => { this.openNewPage('sales') }} className="rctngl">
                                <FontAwesomeIcon icon={faCashRegister} /><br />Sales
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('customers') }} className="rctngl">
                                <FontAwesomeIcon icon={faUsers} />Customers
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('drivers') }} className="rctngl">
                                <FontAwesomeIcon icon={faUser} /><br />Drivers
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('inventory') }} className="rctngl">
                                <FontAwesomeIcon icon={faCubes} />Inventory
                    </button>
                        </div>
                    </div>

                    <div className="row row2">
                        <div className="column">
                            <button onClick={() => { this.openNewPage('returns') }} className="rctngl">
                                <FontAwesomeIcon icon={faUndo} /><br />Returns
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('products') }} className="rctngl">
                                <FontAwesomeIcon icon={faCube} /><br />Products
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('routes') }} className="rctngl">
                                <FontAwesomeIcon icon={faRoute} /><br />Routes
                    </button>
                        </div>
                        <div className="column">
                            <button onClick={() => { this.openNewPage('stats') }} className="rctngl">
                                <FontAwesomeIcon icon={faChartBar} /><br />Statistics
                    </button>
                        </div>
                    </div>


                </div>

            </div>
        )

    }

}

export default OHome